<template>
  <div>
    <div id="treeMount"></div>
  </div>
</template>
<script>
import $ from "jquery";
export default {
  name: "Tree",
  /**
   * data  
   * extend  false
   * hideTopNode true
   * showCheckbox  
   * height ul300px
   */
  props: {
    data: {
      type: Array,
      default: function () {
        return [];
      },
      required: true,
    },
    height: {
      type: Number,
      default: 300,
    },
    extend: {
      type: Boolean,
      default: false,
    },
    hideTopNode: {
      type: Boolean,
      default: false,
    },
    showCheckbox: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // treepropsextend
      treeExtend: false,
      // checkbox
      treeData: [],
    };
  },
  methods: {
    // Tree
    init() {
      //  extendtrue
      if (this.hideTopNode == true) {
        this.treeExtend = true;
      }
      // 
      let ul = $("<ul id='tree'></ul>");
      // 
      this.generatorTree(this.data, ul);
      // ul
      if (this.hideTopNode) {
        ul = $(ul.find("ul")[0]);
      }
      // ulpadding-left
      ul.css({
        "padding-left": "0px",
        "list-style": "none",
        "overflow-y": "auto",
        height: `${this.height}px`,
      });
      // tree
      $("#treeMount").empty();
      $("#treeMount").append(ul);
    },
    /**
     * checkboxboxbox
     * boxbox
     */
    watchCheckbox(checkbox) {
      // 
      let li = $(checkbox).parent("li").parentsUntil("li").parent();
      let checked = true;
      for (let input of li.find("li").find("input")) {
        if ($(input).prop("checked") == false) {
          checked = false;
          break;
        }
      }
      $(li.children("input")).prop("checked", checked);
    },
    /**
     * tree
     */
    watchTreeData() {
      let inputs = $("input:checked");
      this.treeData = [];
      for (let i of inputs) {
        let id = $(i).val();
        this.findTreeData(id, this.data);
      }
    },
    /**
     * tree
     */
    findTreeData(id, data) {
      for (let item of data) {
        if (item.id == id) {
          this.treeData.push(item);
        } else {
          this.findTreeData(id, item.children);
        }
      }
    },
    // 
    generatorTree(data, container) {
      for (let item of data) {
        let li = $("<li></li>");
        // id
        li.attr("id", item.id);
        // checkboxcheckbox
        if (this.showCheckbox) {
          let box = $(`<input type='checkbox' value=${item.id}>`);
          box.css({
            width: "16px",
            height: "16px",
            "vertical-align": "top",
          });
          box.on("click", (event) => {
            let checkbox = event.target;
            let li = $(checkbox).parent("li");
            // 
            if ($(checkbox).prop("checked")) {
              li.find("input").prop("checked", true);
            } else {
              li.find("input").prop("checked", false);
            }
            // checkbox
            this.watchCheckbox(checkbox);
            this.watchTreeData();
          });
          li.append(box);
        }
        // 
        // li
        if (this.showCheckbox) {
          li.on("click", "input", (event) => {
            event.stopPropagation();
            if ($(event.target).prop("checked")) {
              this.$emit("row", item);
            } else {
              this.$emit("row", {});
            }
            this.$emit("rows", this.treeData);
          });
        } else {
          li.on("click", (event) => {
            event.stopPropagation();
            this.$emit("row", item);
          });
        }

        // extendfalsetrue
        if (this.treeExtend == false) {
          li.css({ height: "22.5px", overflow: "hidden" });
        }
        // 
        let span = $("<span></span>");
        // 
        let spanWidth = 0;
        if (item.children && item.children.length > 0) {
          spanWidth += 23;
        }
        if (this.showCheckbox) {
          spanWidth += 23;
        }

        span.css({
          display: "inline-block",
          width: `calc(100% - ${spanWidth}px)`,
          "vertical-align": "middle",
          overflow: "hidden",
          "text-overflow": "ellipsis",
          "white-space": "nowrap",
          "text-align": "left",
        });
        span.text(item.label);
        li.append(span);
        container.append(li);

        // 
        if (item.children && item.children.length > 0) {
          // 
          let img = $("<img />");
          img.attr(
            "src",
            "data:image/png;base64,"
          );
          img.css({
            width: "15px",
            height: "15px",
            margin: "3px 3px 3px 4px",
            "vertical-align": "middle",
            // 
            transform: this.extend ? "rotate(0deg)" : "rotate(-90deg)",
          });
          img.on("click", (event) => {
            event.stopPropagation();
            //   
            if (li.css("height") == "22.5px") {
              li.css({ height: "auto" });
              img.css({
                transform: "rotate(0deg)",
              });
            } else {
              li.css({ height: "22.5px", overflow: "hidden" });
              img.css({
                transform: "rotate(-90deg)",
              });
            }
          });
          li.prepend(img);
          // 
          let ul = $("<ul></ul>");
          ul.css({ "list-style": "none", overflow: "hidden" });
          li.append(ul);
          this.generatorTree(item.children, ul);
        }
      }
    },
  },
  watch: {
    data(val) {
      if (val && val.length > 0) {
        this.init();
      }
    },
  },
  created() {
    this.treeExtend = this.extend;
  },
  mounted() {
    this.init();
  },
};
</script>
<style lang="less" scoped>
ul,
li {
  list-style: none;
}
#treeMount {
  font-size: 16px !important;
}
/deep/::-webkit-scrollbar {
  /**/
  width: 3px; /**/
  height: 1px;
}
/deep/::-webkit-scrollbar-thumb {
  /**/
  border-radius: 3px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #aaa;
}
/deep/::-webkit-scrollbar-track {
  /**/
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: #ededed;
}
</style>